import React from "react";
import {
	Card,
	Button,
	CardText,
	CardDeck,
	CardSubtitle,
	CardBody,
	CardFooter,
	CardHeader,
	CardImg,
} from "reactstrap";
import Slider from "react-slick";
import deviceArray from "./DummyData/Routers";

const Device = (props) => {
	let lgSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 3,
		slidesToScroll: 3,
	};

	let smSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 1,
		slidesToScroll: 1,
	};
	return (
		<React.Fragment>
			<div id="device">
				<div className="h-100 py-5">
					<p className="display-4 text-center my-5 font-weight-bold text-main">
						Devices
					</p>
					<div className="d-none d-lg-block">
						<Slider {...lgSettings} className="mx-5 text-center py-5">
							{deviceCards()}
						</Slider>
					</div>
					<div className="d-lg-none">
						{/* will be hidden on screen more than lg */}
						<Slider {...smSettings} className="mx-5 text-center py-5">
							{deviceCards()}
						</Slider>
					</div>
				</div>
			</div>
		</React.Fragment>
	);
};

export default Device;

export const deviceCards = () => {
	return deviceArray.map((item, i) => (
		<div className="out">
			<Card className="mx-2 text-white bg-main" color="light">
				<div className="mt-5 d-flex justify-content-center">
					<CardImg src={item.pic} className="w-25" />
				</div>
				<CardBody>
					<CardSubtitle className="h2 pb-2 font-weight-bold">
						{item.name}
					</CardSubtitle>
					<CardText className="lead">Once-off.</CardText>
					<CardText className="h2 font-weight-bold">{item.price}</CardText>
					<Button color="light">Buy</Button>
				</CardBody>
			</Card>
		</div>
	));
};
