import React, { useState } from "react";
// import { Redirect } from "react-router-dom";
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	DropdownItem,
	Form,
	FormGroup,
	Input,
} from "reactstrap";
import { Link } from "react-router-dom";
import products from "../DummyData/products";
// import axios from "axios";

export default function ServiceModal(props) {
	// set initail states

	let [username, setUsername] = useState("");
	let [password, setPassword] = useState("");

	let [modal, setModal] = useState(false);

	let toggle = () => setModal(!modal);

	function handleSubmit() {}

	return (
		<React.Fragment>
			<Button onClick={toggle} className="bg-main border-0">
				edit
			</Button>
			<Modal isOpen={modal} toggle={toggle}>
				<Form onSubmit={handleSubmit} className="text-center">
					<ModalHeader toggle={toggle} className="border-0 text-center">
						<p className="text-main">Personal Details</p>
					</ModalHeader>
					<ModalBody>
						<FormGroup className="form-row">
							<select class="custom-select" defaultValue={null}>
								<option>Open this select menu</option>
								{products.map((item, i) => (
									<option key={i} value={item}>
										{item.name} anytime + {item.name} night shift
									</option>
								))}
							</select>
						</FormGroup>
					</ModalBody>
					<ModalFooter className="border-0">
						<Button
							type="button"
							onClick={handleSubmit}
							className="bg-main border-0"
							block
						>
							Save
						</Button>
						<Button color="secondary" onClick={toggle} block>
							Cancel
						</Button>
					</ModalFooter>
				</Form>
			</Modal>
		</React.Fragment>
	);
}
