import React from "react";
import Navigation from "../Components/Navigation";

function TermsAndConditions() {
	return (
		<React.Fragment>
			<Navigation isHomeNav={false} />
			<div
				style={{ minHeight: "70vh" }}
				className="row my-5 p-5 justify-content-around"
				id="about"
			>
				<div className=" col-12 col-lg-8 py-3 text-center">
					<h1 className="font-weight-bold my-4 ml-5">Terms and Conditions</h1>
					<hr className="w-50 style-two mb-5" />
					<div className="text-justify mt-5">
						<p>
							Unused monthly data(Excluding Telkom 10+10GB) as well as topup
							data will rollover for 1 month. At the start of each month the
							rolled over data (monthly and topup) will be used first, once
							exhausted the monthly allocation will be used.{" "}
						</p>
						<p>
							Night time data (where applicable) is free data that is available
							to use everyday between 12am and 6am. Data used during these hours
							will be deducted from the Night time available data balance. When
							Night time data is depleted, the available monthly data will be
							used.
						</p>
						<p>
							You are able to purchase additional data should your allocation
							run out.
						</p>
						<p>
							The service is a monthly billed service which will continue on a
							month to month basis until cancelled. Cancellations require a
							calendar months' notice and to cancel send email to
							accounts@sbwldata.co.za, cancellations cannot be set on the last 2
							days of each month. The service can be set to upgrade or downgrade
							at any time (before the 25th of each month) but the new service
							will only be effective from the 1st of the following month
							provided no Service Change rules are in place
						</p>
					</div>
				</div>
			</div>
		</React.Fragment>
	);
}

export default TermsAndConditions;
