import React, { useState } from "react";
// import { Redirect } from "react-router-dom";
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	DropdownItem,
	Form,
	FormGroup,
	Input,
} from "reactstrap";
import { Link } from "react-router-dom";
// import axios from "axios";

export default function PersonalModal(props) {
	// set initail states

	let [username, setUsername] = useState("");
	let [password, setPassword] = useState("");

	let [modal, setModal] = useState(false);

	let toggle = () => setModal(!modal);

	function handleSubmit() {}

	return (
		<React.Fragment>
			<Button onClick={toggle} className="bg-main border-0">
				edit
			</Button>
			<Modal isOpen={modal} toggle={toggle}>
				<Form onSubmit={handleSubmit} className="text-center">
					<ModalHeader toggle={toggle} className="border-0 text-center">
						<p className="text-main">Personal Details</p>
					</ModalHeader>
					<ModalBody>
						<FormGroup>
							<Input
								name="name"
								type="text"
								placeholder="Name"
								autofocus
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
							/>
						</FormGroup>
						<FormGroup>
							<Input
								name="surname"
								type="text"
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Surname"
							/>
						</FormGroup>
						<FormGroup>
							<Input
								name="id"
								type="text"
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="ID number"
							/>
						</FormGroup>
						<FormGroup>
							<Input
								name="email"
								type="email"
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Email address"
							/>
						</FormGroup>
						<FormGroup>
							<Input
								name="cellphone"
								type="text"
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Mobile number"
							/>
						</FormGroup>
					</ModalBody>
					<ModalFooter className="border-0">
						<Button
							type="button"
							onClick={handleSubmit}
							className="bg-main border-0"
							block
						>
							Save
						</Button>
						<Button color="secondary" onClick={toggle} block>
							Cancel
						</Button>
					</ModalFooter>
				</Form>
			</Modal>
		</React.Fragment>
	);
}
