import React from "react";
import {
	Card,
	Button,
	CardFooter,
	CardHeader,
	CardBody,
	CardDeck,
	CardText,
	CardImg,
} from "reactstrap";
// import BGI from "../Images/mesh.png";
import Col from "reactstrap/lib/Col";
import Row from "reactstrap/lib/Row";
import Slider from "react-slick";
import productsArray from "./DummyData/products";
import sim from "../Images/sim.png";
import Details from "./Details";

const Offers = (props) => {
	let lgSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 3,
		slidesToScroll: 3,
	};

	let smSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 1,
		slidesToScroll: 1,
	};

	return (
		<React.Fragment>
			<div className="py-5" id="deals">
				<p className="display-4 text-center my-5 font-weight-bold text-main">
					Deals
				</p>
				{/* will be hidden on screen less than lg */}
				<div className="d-none d-lg-block">
					<Slider {...lgSettings} className="mx-5 text-center py-5">
						{productCards()}
					</Slider>
				</div>
				<div className="d-lg-none">
					{/* will be hidden on screen more than lg */}
					<Slider {...smSettings} className="mx-5 text-center py-5">
						{productCards()}
					</Slider>
				</div>
			</div>
		</React.Fragment>
	);
};

export default Offers;

export const productCards = () => {
	return productsArray.map((item, i) => (
		<div className="out">
			<Card className="mx-2 flip_card border-0">
				<div className="card_front text-white h2 rounded w-100">
					<CardBody className="rounded-0 text-center">
						<Row>
							<Col>
								<p className="font-weight-bold">
									{item.name} <br />
									<small className="text-muted h5">anytime</small>
								</p>
							</Col>
							<div className="d-none d-md-block">+</div>
							<Col>
								<p className="font-weight-bold">
									{item.name} <br />
									<small className="text-muted h5">night data</small>
								</p>
							</Col>
						</Row>
						<div className="d-flex justify-content-center">
							<img src={sim} style={{ width: "25%" }} />
						</div>
					</CardBody>
					{/* <CardImg src={sim} className="bg-dark center" /> */}
					<CardFooter className="border-0 bg-transparent">
						<Col className="h2 text-white font-weight-bold">{item.price}</Col>
						<Details />
					</CardFooter>
				</div>
				<div className="card_back text-white rounded h-100">
					<CardBody>
						<div className="text-left">
							<ul>
								<li>Month-to-Month</li>
								<li> 3 Month data rollover</li>
								<li>No out-of-bundle</li>
								<li>
									LTE ready, just insert sim into deivce and you are ready
								</li>
								<li>3 in 1 breakable sim fits into any device</li>
							</ul>
						</div>
					</CardBody>
				</div>
			</Card>
		</div>
	));
};
