import React from "react";
import { Button, Input, FormGroup, Form } from "reactstrap";
import PersonalModal from "../Components/DashComponents/PersonalModal";
import ServiceModal from "../Components/DashComponents/ServiceModal";
import { Link } from "react-router-dom";
import appAuth from "../Authentication/base";

function CustomerDash() {
	function handleLogout() {
		appAuth.auth().signOut();
	}
	return (
		<React.Fragment>
			<div className="h-100 p-5">
				<p className="h1 text-main">
					Welcome Xhantilomzi{" "}
					<small className="h5 float-right">
						<Button onClick={handleLogout}>Logout</Button>
					</small>
				</p>
				<hr />
				<div className="row">
					<div className="col-12 col-lg-4 mt-5">
						<p className="h3 text-main">Personal details</p>
						<p className="text-muted">Xhantilomzi Mfecane</p>
						<p className="text-muted">xxxxxxxxxxxx</p>
						<p className="text-muted">081 000 000</p>
						<p className="text-muted">example@email.co.za</p>
						<p className="text-muted">Address</p>
						<PersonalModal />
					</div>
					<div className="col-12 col-lg-4 mt-5">
						<p className="h3 text-main">Services</p>
						<p className="text-muted">
							10 GB + 10 GB <small>month-to-month</small>
						</p>
						<p className="text-muted">R 150,00</p>
						<ServiceModal />
					</div>
					<div className="col-12 col-lg-4 mt-5">
						<p className="h3 text-main">Manage account</p>
						<Form autoComplete="off">
							<FormGroup className="mt-3">
								<Input type="password" placeholder="current password" />
							</FormGroup>
							<FormGroup className="mt-3">
								<Input type="password" placeholder="new password" />
							</FormGroup>
							<FormGroup className="mt-3">
								<Input type="password" placeholder="confirm password" />
							</FormGroup>
							<Button className="bg-main border-0">change</Button>
						</Form>
					</div>
				</div>
			</div>
		</React.Fragment>
	);
}

export default CustomerDash;
