import React from "react";
import { Jumbotron, Button } from "reactstrap";
import Navigation from "./Navigation";
import BGI from "../Images/homeOffice.jpg";
import Logo from "../Images/Logo.png";
import { HashLink } from "react-router-hash-link";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowDown } from "@fortawesome/free-solid-svg-icons";

const Hero = (props) => {
	return (
		<Jumbotron
			style={{
				height: "100vh",
				backgroundImage: `url(${BGI})`,
				backgroundSize: "cover",
				backgroundPosition: "top",
			}}
			className="p-0 text-white rounded-0 bg-danger text-center"
		>
			<div className="h-100 hero-content">
				<Navigation isHomeNav={true} />
				<div className="d-flex align-items-center justify-content-center h-100">
					<div>
						<img src={Logo} alt="SBWL DATA" style={{ width: "30%" }} />
						<h1 className="display-2 font-weight-bold heading mt-4">
							SBWL Data
						</h1>
						<hr className="my-2 style-one" color="white" />
						<p className="h2 mb-5 subText">The right mobile network for you.</p>
						<p className="lead">
							<HashLink to="/#deals">
								<Button className="rounded-circle bg-main text-white border-0 mt-5">
									<FontAwesomeIcon
										icon={faArrowDown}
										className="animate__animated animate__shakeY"
									/>
								</Button>
							</HashLink>
						</p>
					</div>
				</div>
			</div>
		</Jumbotron>
	);
};

export default Hero;
