import React, { useState } from "react";
// import { Redirect } from "react-router-dom";
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	DropdownItem,
	Form,
	FormGroup,
	Input,
} from "reactstrap";
// import axios from "axios";

export default function Details(props) {
	let [modal, setModal] = useState(false);

	let toggle = () => setModal(!modal);

	return (
		<React.Fragment>
			<Button
				color="light"
				onClick={toggle}
				outline
				className="text-center d-lg-none"
			>
				View
			</Button>
			<Modal isOpen={modal} toggle={toggle} className="bg-main text-white">
				{/* <ModalHeader toggle={toggle} className="border-0 text-center">
						<p>Please Login</p>
					</ModalHeader> */}
				<ModalBody className="bg-main">
					<ul>
						<li>Month-to-Month</li>
						<li> 3 Month data rollover</li>
						<li>No out-of-bundle</li>
						<li>LTE Ready, just insert sim into deivce and you are ready</li>
						<li>3 in 1 breakable sim fits into any device</li>
					</ul>
				</ModalBody>
				<ModalFooter className="border-0 bg-main">
					<Button color="light" onClick={toggle} block>
						Cancel
					</Button>
				</ModalFooter>
			</Modal>
		</React.Fragment>
	);
}
